<template>
  <div class="home">
    <div :class="{'home__mask': showType}"></div>

    <div class="home__select">
      <div class="home__select-pinpai" @click="changeType">
        <span>车险品牌</span>
        <i class="iconfont icon-down-copy-copy" :class="{'down--ative': showType}"></i>  
      </div>
      <div class="home__select-logo">
        <img class="home__select-logo-1" src="@/assets/7.jpg">
        <img class="home__select-logo-2" src="@/assets/8.jpg">
      </div>
      <div class="home__select-fenlei" :class="{ 'home__select-fenlei--show': showType }">
        <div class="home__select-fenlei-name">
          <p :class="{ 'home__select-fenlei-name--active': types.includes(0) }" @click="chosetype(0)">中国人寿</p>
          <p :class="{ 'home__select-fenlei-name--active': types.includes(1) }" @click="chosetype(1)">中国平安</p>
          <p :class="{ 'home__select-fenlei-name--active': types.includes(2) }" @click="chosetype(2)">珠峰车险</p>
          <p :class="{ 'home__select-fenlei-name--active': types.includes(3) }" @click="chosetype(3)">大地保险</p>
        </div>
        <div class="home__select-fenlei-footer">
          <p class="home__select-fenlei-footer-cancel" @click="showType = false">取消</p>
          <p class="home__select-fenlei-footer-submit" @click="submit">确定</p>
        </div>
      </div>
    </div>
    <div class="home__select-type">
      <span :class="{'home__select-type--active': type === 1}" @click="type = 1">所有车险</span>
      <span :class="{'home__select-type--active': type === 2}" @click="type = 2">驾乘意外险</span>
      <span :class="{'home__select-type--active': type === 3}" @click="type = 3">三者险超赔险</span>
    </div>    
    <ul class="home__list">
      <li class="home__list-card" @click="choseCard">
        <div class="home__list-card-img">
          <img src="@/assets/10.jpg">
        </div>
        <div class="home__list-card-detail">
          <p class="home__list-card-detail-title">珠峰三者险超赔（无免赔额度）</p>
          <p class="home__list-card-detail-time">保险期限1年</p>
          <p class="home__list-card-detail-money">
            <i class="iconfont icon-renminbiyangjiaofuhao"></i>
            <span>150.00</span>
          </p>
        </div>
      </li>
      <van-button disabled type="primary">禁用状态</van-button>
      <van-button type="default">默认按钮</van-button>
      <van-button type="primary">主要按钮</van-button>
      <van-button type="warning">警告按钮</van-button>
      <van-button type="danger">危险按钮</van-button>
      <van-field v-model="value" placeholder="请输入用户名" />
    </ul>
    <div @click="back">上一步</div>
    <div @click="next">下一步</div>
    <Step :lists="lists" :index="index"/>
  </div>
</template>

<script>
import Step from "@/components/step.vue";

export default {
  data() {
    return {
      type: 1,
      showType: false,
      types: [],

      index: 1,
      lists: ['个人信息', '联系人信息', '银行卡信息'],
      value: ""
    };
  },
  components: {
    Step
  },
  methods: {
    back() {
      this.index = this.index > 1 && this.index <= this.lists.length ?  this.index - 1 : 1;
    },
    next() {
      this.index = this.index >= 1 && this.index < this.lists.length ?  this.index + 1 : this.lists.length;
    },
    changeType() {
      this.showType = !this.showType;
    },
    chosetype(val) {
      if(this.types.includes(val)) {
        this.types.forEach((item, index) => {
          if( item == val ) {
            this.types.splice(index, 1);
          }
        })
      } else {
        this.types.push(val);
      }
    },
    submit() {
      this.showType = false;
    },
    choseCard() {
      this.$router.push("/detail");
    }
  }
};
</script>

<style lang="scss">
$prefix: home;

.#{$prefix} {
  &__mask {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    height: calc(100vh - 3rem);
    background: black;
    opacity: 0.5; 
    z-index: 10;
  }
  &__select-type {
    display: flex;
    align-items: center;
    padding-left: 0.2rem;
    height: 1rem;
    font-size: 0.3rem;
    color: #999;
    span {
      margin-right: 0.3rem;
    }
    &--active {
      position: relative;
      font-weight: 700;
      color: #000;
      &::after {
        content: "";
        position: absolute;
        top: 0.4rem;
        display: block;
        width: 100%;
        height: 0.05rem;
        background: #f08700;
      }
    }
  }
  &__select {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 0.3rem 0 0.2rem;
    height: 1rem;
    border-bottom: solid 1px #e5e5e5;
    &-fenlei {
      position: absolute;
      overflow: hidden;
      top: 1.1rem;
      left: 0;
      width: 100%;
      height: 0rem;
      background: white;
      transition: all .5s ease;
      z-index: 11;
      &-name {
        display: flex;
        flex-wrap: wrap;
        font-size: 0.3rem;
        color: #000;
        &--active {
          color: #ff7f0e;
        }
        p {
          box-sizing: border-box;
          flex: 0 0 auto;
          padding: 0.3rem 0 0.3rem 0.2rem;
          width: 50%;
        }
      }
      &-footer {
        position: absolute;
        width: 100%;
        bottom: 0;
        display: flex;
        height: 1rem;
        font-size: 0.32rem;
        p {
          flex: 1 1 auto;
          display: flex;
          align-items: center;
          justify-content: center;
        }
        &-cancel {
          background: #eee;
          color: #000;
        }
        &-submit {
          background: #ff7f0e;
          color: #ffffff;
        }
      }
    }
    &-fenlei--show {
      height: 5rem;
    }
    &-pinpai {
      display: flex;
      align-items: center;
      font-size: 0.3rem;
      i {
        font-size: 0.3rem;
        display: inline-block;
        transform:rotate(0deg);
        transition: all .3s linear;
      }
      .down--ative {
        transform: rotate(90deg);
      }
    }
    &-logo {
      position: relative;
      width: 2rem;
      height: 0.7rem;
      img {
        position: absolute;
        top: 0;
        width: 0.7rem;
        height: 0.7rem;
        border-radius: 100%;
        object-fit: cover;
      }
      &-1 {
        left: 0.65rem;
      }
      &-2 {
        left: 1.2rem;
      }
    }
  }
  &__list {
    height: calc(100vh - 2.9rem);
    overflow: auto;
    &-card {
      display: flex;
      padding: 0.4rem 0 0.4rem;
      margin-left: 0.2rem;
      border-bottom: 1px solid #e5e5e5;
      &-detail {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        font-size: 0.36rem;
        &-title {
          font-weight: 700;
        }
        &-time {
          font-size: 0.3rem;
          color: #666;
        }
        &-money {
          font-size: 0.42rem;
          color: #ff7f0e;
          span::after {
            content: "起";
            font-size: 0.24rem;
            margin-left: 0.1rem;
          }
        }
      }
      &-img {
        flex: 0 0 auto;
        margin-right: 0.27rem;
        height: 2.1rem;
        width: 2.8rem;
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
    }
  }
}
</style>
