import Vue from "vue"
import VueRouter from "vue-router"

import Home from "@/views/home/index.vue";
import Detail from "@/views/detail/index.vue";

Vue.use(VueRouter)

const router = new VueRouter({
  // mode: 'history',
  routes:[
    {
      path: "/",
      name: "home",
      component: Home,
      meta: {
        nav: "找车险"
      }
    },
    {
      path: "/detail",
      name: "detail",
      component: Detail,
      meta: {
        nav: "详情"
      }
    }
  ]
})

export default router;