<template>
  <div class="nav-box">
    <div class="nav">
      <i class="iconfont icon-back" @click="goBack"></i>
      <div class="nav__name">{{ nav }}</div>
      <div class="nav__status">
        <div v-if="nav == '找车险'">
          <i class="iconfont icon-baodan"></i>
          <span>保单</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  watch: {
    "$route"(val) {  
      this.nav = val.meta.nav;
    }
  },
  data() {
    return {
      nav: ""
    }
  },
  methods: {
    goBack() {
      this.$router.go(-1)
    }
  },
  created() {
    this.nav =  this.$route.meta.nav;
  }
}
</script>

<style lang="scss">
$prefix: nav;

.nav-box {
  height: 0.85rem;
}
.#{$prefix} {
  position: fixed;
  top: 0;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 0.85rem;
  font-size: 0.36rem;
  background: white;
  z-index: 100;
  .icon-back {
    box-sizing: border-box;
    padding-left: 0.2rem;
    width: 33.33%;
    font-size: 0.32rem;
    font-weight: 700;
  }
  .icon-baodan {
    font-size: .4rem;
    margin-right: 0.1rem;
  }
  &__status {
    width: 33.33%;
    color: #f08700;
    div {
      float: right;
      margin-right: 0.4rem;
    }
  }
  &__name {
    width: 33.33%;
    font-weight: 700;
    text-align: center;
  }
}
</style>