<template>
  <div class="step__box">
    <div
        class="step__box-line"
       :class="{
        'step__box-line1': index == 1, 
        'step__box-line2': index == 2, 
        'step__box-line3': index == 3 
      }">
      <div v-for="(one, index) in lists" :key="index"></div>
    </div>
    <ul :class="{
        'step__box-ul1': index == 1, 
        'step__box-ul2': index == 2, 
        'step__box-ul3': index == 3 
      }">
      <li v-for="(one, index) in lists" :key="index">
        <p>{{ index + 1 }}</p>
        <p>{{ one }}</p>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    lists: {
      type: Array,
      required: true 
    },
    index: {
      type: Number,
      default: 1
    }
  },
  data() {
    return {};
  }
};
</script>

<style lang="scss">
$prefix: step;

.#{$prefix} {
  &__box {
    position: relative;
    ul {
      position: absolute;
      display: flex;
      justify-content: space-between;
      box-sizing: border-box;
      padding: 0.2rem;
      width: 100%;
      li {
        display: flex;
        flex-direction: column;
        align-items: center;
        list-style: none;
        p:nth-child(1) {
          width: 0.6rem;
          height: 0.6rem;
          line-height: 0.6rem;
          text-align: center;
          border-radius: 100%;
          color: #fff;
          font-size: 0.2rem;
        }
        p:nth-child(2) {
          margin-top: 0.2rem;
          font-size: 0.2rem;
        }
      }
    }
    &-ul1 {
      li:first-child {
        p:nth-child(1) {
          background: #169BD5;
        }
        p:nth-child(2) {
          color: #169BD5;
        }
      }
      li {
        p:nth-child(1) {
          background: #ccc;
        }
        p:nth-child(2) {
          color: #ccc;
        }
      }
    }
    &-ul2 {
      li {
        p:nth-child(1) {
          background: #169BD5;
        }
        p:nth-child(2) {
          color: #169BD5;
        }
      }
      li:last-child {
        p:nth-child(1) {
          background: #ccc;
        }
        p:nth-child(2) {
          color: #ccc;
        }
      }
    }
    &-ul3 {
      li {
        p:nth-child(1) {
          background: #169BD5;
        }
        p:nth-child(2) {
          color: #169BD5;
        }
      }
    }
    
    &-line {
      position: absolute;
      display: flex;
      top: 0.45rem;
      left: 0.6rem;
      width: calc(100% - 1.3rem);
      height: 0.1rem;
      div:first-child {
        flex: 1;
      }
      div {
        flex: 2;
        border-top: 1px solid #ccc;
        border-bottom: 1px solid #ccc;
      }
      div:last-child {
        flex: 1;
      }
    }
    &-line1 {
      div:first-child {
        background: #169BD5;
      }
      div {
        flex: 2;
        border-top: 1px solid #ccc;
        border-bottom: 1px solid #ccc;
      }
    }
    &-line2 {
      div:last-child {
        background: #fff;
      }
      div {
        background: #169BD5;
      }
    }
    &-line3 {
      div {
        background: #169BD5;
      }
    }
    
  }
}
</style>
